.product {
    padding: 5% 0 10% 2%;
    overflow: auto;
    white-space: nowrap;
    background-color: #eee;
    border-bottom: 1px solid #e0e0e0;
    width: 98%;
    float: left;
}
.product-card {
    width: 42%;
    margin: 0 1% 0 2%;
    padding: 0!important;
    display: inline-block;
    overflow: hidden;
    position: relative;
    border-radius: 14px;
    /* background: transparent; */
    box-shadow: 0 2px 2px 0 rgb(0 0 0 / 14%), 0 3px 1px -2px rgb(0 0 0 / 12%), 0 1px 5px 0 rgb(0 0 0 / 20%);
}
.product-card:nth-child(odd) {
    margin-bottom: -4%;
}
.card-title{
    font-size: 12px;
}
.listProductCrouselcard.product {
    position: relative;
    top: 40px;
}
.productImg {
    width: 100%;
    /* float: left; */
     color: #ccc;
    height: 15vh;
    overflow: hidden;
    background-size: cover!important;
    border-radius: 0;
    box-shadow: none;
    /* float: left; */
    background-color: #ccc;
    overflow: hidden;
    background-size: cover!important;
    background-position-x: 50%;
}