
.btnshow{
    text-align:center;margin-bottom: 20px;margin-top: 20px;
}
.bigger_int {
    width: 100% !important;
    float: left !important;
    height: -webkit-fill-available;
    position: absolute !important;
    top: 0px !important;
    left: 0px !important;
    /* z-index: 99999 !important; */
    font-family: sans-serif !important;
   
    background-size: cover !important;
    background-position-x: 60% !important;
 }
 .bigger_int .inner_main {
    width: 100% !important;
    height: 100% !important;
    padding: 0px !important;
    position: relative !important;
 }

 .inner_main .header_img {
    width: 30% !important;
    float: left !important;
    background: #0E161E !important;
    position: relative !important;
    border-radius: 30px !important;
    overflow: hidden !important;
    margin: 5% 1% -16% 1% !important;
 }

 .inner_main .header_img img {
    width: 100% !important;
    padding: 0px !important;
 }

 .major_nav {
    /* width: 320px !important;
    height: 480px; */
    float: left !important;
    position: absolute;
    /* bottom: 0px;
    right: 0px; */
    background-color: #000;
    z-index: 9999;
    border: 2px solid black;
 }
 #rwadx_tamil_pg_deal {
    width: 100% !important;
    float: left !important;
 }
 .video-player-container {
    width: 100% !important;
    float: left !important;
    margin: 0% !important;
    position: relative !important;
    overflow: hidden !important;
    transition: 0.2s ease-out;
 }
 .apv_bottomframe_ab {
    width: 100% !important;
    float: left !important;
    position: fixed !important;
    bottom: -10px !important;
    left: 0px !important;
 }

 .pocket-main-title {
    display: flex;
    justify-content: space-between;
    color: #fff;
    align-items: center;
    padding: 5px 10px;
    background: deeppink;

 }
 .pocket-title-set {
    /* float: left; */
 }

 .pocket-title-set p {
    color: #fff !important;

 }
 #icon-caret {
    transform: rotate(180deg) !important;
 }

 #ddlclick2,
 #ddlclick1 {
    box-shadow: none !important;

 }
 #bigger_int .btnclick {
    background: deeppink;
    border: 1px;
    color: #fff;

 }
 .marquee {
    height: 25px;
    width: 420px;
    overflow: hidden;
    position: relative;
 }
 .marquee div {
    display: block;
    width: 400px;
    text-align: center;
    height: 30px;
    position: absolute;
    overflow: hidden;
    animation: marquee 5s linear infinite;
 }
 @keyframes marquee {
    0% {
       left: 0;
    }

    100% {
       left: -100%;
    }
 }
 #pocket-video-section {
    opacity: 1;
    -webkit-transition: opacity 2s;
    -moz-transition: opacity 2s;
    transition: opacity 2s;
 }
 #pocket-video-section.fade {
    opacity: 0;

 }
 .fadehide {
    opacity: 1;
 }
